import request from '@/plugin/axios';
export default {
  name: 'role-manage',
  data: function data() {
    return {
      filename: __filename,
      activeName: 'first',
      activeRole: '',
      roleList: [],
      userList: [],
      roleMember: [],
      checkAll: false,
      checkedCities: [],
      menu: [],
      isIndeterminate: false
    };
  },
  watch: {
    'activeRole': function activeRole() {
      this.getRoleMember();
      this.getRoleHasMenu();
    }
  },
  methods: {
    handleClick: function handleClick(tab, event) {
      console.log(tab, event);
    },
    handleCheckAllChange: function handleCheckAllChange(val, data) {
      var child = [];

      if (data.children && data.children.length > 0) {
        child = data.children.map(function (item) {
          return item.id;
        });
      }

      if (val) {
        this.checkedCities.push.apply(this.checkedCities, child);
      } else {
        var temp = JSON.parse(JSON.stringify(this.checkedCities));
        this.checkedCities = temp.filter(function (check) {
          return child.findIndex(function (item) {
            return check === item;
          }) < 0;
        });
      }
    },
    choose: function choose(item) {
      var id = item.id;
      this.activeRole = id;
      console.log(item);
    },
    getRoleList: function getRoleList() {
      var _this = this;

      request.get('/role/findAll').then(function (res) {
        if (res.code === 0) {
          _this.roleList = res.data;

          _this.$nextTick(function () {
            _this.activeRole = _this.roleList[0] ? _this.roleList[0].id : '';
          });
        }
      });
    },
    getRoleMember: function getRoleMember() {
      var _this2 = this;

      var roleId = this.activeRole;

      if (roleId) {
        request.get("/role/member/".concat(roleId)).then(function (res) {
          if (res.code === 0) {
            _this2.roleMember = res.data;
          }
        });
      }
    },
    getUserList: function getUserList() {
      var _this3 = this;

      request.get('/user/findAll').then(function (res) {
        if (res.code === 0) {
          _this3.userList = res.data;
        }
      });
    },
    // 添加成员
