        var params = movedKeys;
        request.post("/role/member/".concat(this.activeRole), params).then(function (res) {
          if (res.code === 0) {
            _this4.$message({
              message: '添加成功',
              type: 'success'
            });
          }
        });
      }
    },
    // 移除成员
    removeMember: function removeMember(movedKeys) {
      var _this5 = this;

      if (movedKeys && movedKeys.length > 0 && this.activeRole) {
        var params = movedKeys;
        request.post("/role/deleteMember/".concat(this.activeRole), params).then(function (res) {
          if (res.code === 0) {
            _this5.$message({
              message: '移除成功',
              type: 'success'
            });
          }
        });
      }
    },
    handleMemberChange: function handleMemberChange(value, direction, movedKeys) {
      if (direction === 'right') {
        // addMember()
        this.addMember(movedKeys);
      } else {
        // removeMember()
        this.removeMember(movedKeys);
      }
    },
    saveRoleMenu: function saveRoleMenu() {
      var _this6 = this;

      if (this.checkedCities.length > 0) {
