        request.get("/role/roleHasMenu/".concat(this.activeRole)).then(function (res) {
          if (res.code === 0) {
            _this7.checkedCities = res.data;
          }
        });
      }
    },
    getMenuTree: function getMenuTree() {
      var _this8 = this;

      request.get("/menu/menuTree").then(function (res) {
        if (res.code === 0) {
          _this8.menu = res.data;
        }
      });
    }
  },
  created: function created() {
    this.getRoleList();
    this.getUserList();
    this.getMenuTree();
  }
};